import { set } from 'lodash';
import React, { useState, useContext, useEffect } from 'react';
import { SocketContext } from '../cmps/context-provider'

export const Options = ({ children, callerName, callerId }) => {

   const { me, setMe, callAccepted, name, onlineUser, setName, callEnded, leaveCall, callUser } = useContext(SocketContext);

   const onCallUser = () => {
      callUser(callerId)
   }

   return (
      <div className='options'>
         {callAccepted && !callEnded
            ? (<button onClick={leaveCall} >Hang Up</button>)
            : (callerName && <button onClick={onCallUser} > Call {callerName}</button>)
         }

         {children}
      </div>
   )
}


