import React, { createContext, useState, useRef, useEffect } from 'react'
import { socketService } from '../services/socket.service'
import Peer from 'simple-peer'

const SocketContext = createContext()

const ContextProvider = ({ children, username }) => {

   const [callAccepted, setCallAccepted] = useState(false) // 
   const [callEnded, setCallEnded] = useState(false)
   const [stream, setStream] = useState()
   const [name, setName] = useState('')
   const [call, setCall] = useState({})
   const [onlineUser, setOnlineUser] = useState({})
   const [me, setMe] = useState('')

   const myVideo = useRef()
   const userVideo = useRef()
   const connectionRef = useRef()

   useEffect(() => {

      // bulid in navigator to get permission from user
      getVideoPermission()

      // listen to 'me' event and get the id from backend
      socketService.on('me', (id) => {
         console.log('socket.id', id)
         socketService.emit('notifyImOnline', { id })
         return setMe(id)
      })

      // listen to 'callUser' event, recieve a call
      socketService.on('callUser', ({ from, name: callerName, signal }) => {
         console.log('Get a call from ' + from)
         setCall({ isReceivingCall: true, from, name: callerName, signal })
      })

      socketService.on('onlineUser', ({ data }) => {
         console.log('online user ID: ' + data.id)
         setOnlineUser(data.id)
      })

   }, [])

   const getVideoPermission = async () => {
      try {
         const currentStream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true })
         // set the stream of the video
         setStream(currentStream)
         // why also useRef? chaeck about srcObject
         setTimeout(() => { myVideo.current.srcObject = currentStream }, 1)
      } catch (error) {
         console.log(error)
      }
   }

   const answerCall = () => {
      setCallAccepted(true)

      // set new peer using the Peer library, its work like sockets
      const peer = new Peer({ initiator: false, trickle: false, stream }) // initiator: we not calling

      peer.on('signal', (data) => {
         socketService.emit('answerCall', { signal: data, to: call.from })
      })

      peer.on('stream', (currentStream) => {
         userVideo.current.srcObject = currentStream
      })

      peer.signal(call.signal)
      connectionRef.current = peer
   }

   const callUser = (id) => {
      const peer = new Peer({ initiator: true, trickle: false, stream }) // initiator: we are calling

      peer.on('signal', (data) => {
         socketService.emit('callUser', { userToCall: id, signalData: data, from: me, name })
      })

      peer.on('stream', (currentStream) => {
         userVideo.current.srcObject = currentStream
      })

      socketService.on('callAccepted', (signal) => {
         setCallAccepted(true)
         peer.signal(signal)
      })

      connectionRef.current = peer
   }

   const leaveCall = () => {
      setCallEnded(true)
      connectionRef.current.destroy()
      window.location.reload()
   }

   return (
      <SocketContext.Provider value={{ call, callAccepted, myVideo, userVideo, stream, name, setName, onlineUser, callEnded, setMe, me, callUser, leaveCall, answerCall, }}>
         {children}
      </SocketContext.Provider>
   )
}

export { ContextProvider, SocketContext }