import React, { useState, useEffect } from 'react';
// import { io } from 'socket.io-client'

import { socketService } from '../services/socket.service'
import { ContextProvider } from '../cmps/context-provider'

import { VideoPlayer } from '../cmps/videoPlayer'
import { Options } from '../cmps/options'
import { Notifications } from '../cmps/notifications'
import { SideBar } from '../cmps/side-bar'

// const baseUrl = (process.env.NODE_ENV === 'production') ? '' : '//localhost:3030'
// const socket = io(baseUrl)

export const VideoChat = () => {

   const [username, setUsername] = useState('')
   const [callerId, setCallerId] = useState('')
   const [callerName, setCallerName] = useState('')
   const [isRegisterd, setIsRegisterd] = useState(false)

   useEffect(() => {
      socketService.on('onlineUsername', ({ username, id }) => {
         console.log('ID', id);
         console.log('callerName', username);
         setCallerName(username)
         setCallerId(id)
      })
   }, [])

   const onSetName = (ev) => {
      ev.preventDefault();
      setUsername(ev.target.value)
   }

   const onStartChat = (ev) => {
      ev.preventDefault();
      setIsRegisterd(true)
      socketService.emit('sendUserName', username)
   }

   return (
      <section className="video-chat">
         <h1>Video Chat</h1>
         {!isRegisterd && <div className="input">
            <input type="text" id='name' placeholder='Your name' value={username} onChange={onSetName} />
            <button onClick={onStartChat}>Join chat</button>
         </div>}

         <ContextProvider username={username} >
            <SideBar username={username} callerName={callerName} />
            <VideoPlayer isRegisterd={isRegisterd} username={username} />
            <Options callerName={callerName} username={username} callerId={callerId}>
               <Notifications />
            </Options>
         </ContextProvider>
      </section>
   )
}

